/**
 * resizeChange
 * 监听屏幕尺寸变化
 */
//map如果卸载还在，影响泄露，所以用weakMap
const map = new WeakMap()
const resizOb = new ResizeObserver((entries) => {
    entries.forEach(entry => {
        const handler = map.get(entry.target)
        handler && handler({
            width: entry.borderBoxSize[0].inlineSize,
            height: entry.borderBoxSize[0].blockSize
        })
    })
})
export default {
    mounted(el, binging) {
        map.set(el, binging.value)
        resizOb.observe(el)
    },
    onUnmounted(el) {
        resizOb.unobserve(el)
    }
}