<template>
  <div class="sassMedia" v-size-ob="handleSizeChange"></div>
  <div class="resize-box"></div>
</template>
<script setup>
import { ref } from "vue";
// 引入自定义指令
import sizeOb from "@/utils/resizeChange";
const vSizeOb = sizeOb;
const width = ref(50);
const height = ref(50);
function handleSizeChange(event) {
  width.value = event.width / 2;
  height.value = event.height / 2;
  console.log("属性", event);
}
const searchValue = ref("");
</script>
<style lang="scss" scoped>
//断点列表
$breakPoint: (
  "phone": (
    320px,
    480px,
  ),
  "pad": (
    481px,
    768px,
  ),
  "noteBook": (
    769px,
    1024px,
  ),
  "desktop": (
    1025px,
    1200px,
  ),
  "bigDesktop": 1201px,
);
//混合，书写通用的媒体查询样式
@mixin respond-to($breakPointName) {
  $map: map-get($breakPoint, $breakPointName);
  @if type-of($value: $map) == "list" {
    $min: nth($map, 1);
    $max: nth($map, 2);
    @media (min-width: $min) and (max-width: $max) {
      @content;
    }
  } @else {
    @media (min-width: $map) {
      @content;
    }
  }
}
.sassMedia {
  width: 100%;
  height: 500px;
  background-color: aqua;
  @include respond-to("phone") {
    height: 200px;
  }
  @include respond-to("pad") {
    height: 300px;
  }
}
.resize-box {
  $w: calc(v-bind(width) * 1px);
  $h: calc(v-bind(height) * 1px);
  width: $w;
  height: $h;
  background-color: #f11515;
}
</style>